<script>
export default {
    name: "ProgressBar",
    functional: true,
    props: {
        target: {
            type: Number,
            default: 1000,
        },
        current: {
            type: Number,
            default: 500,
        },
        size: {
            type: String,
            default: "md",
            validator: function (value) {
                return ["sm", "md"].indexOf(value) != -1;
            },
        },
        maxWidth: {
            type: String,
        },
    },
    render: function (h, { data, props, scopedSlots: slots }) {
        /** –ü—Ä–æ—Ü–µ–Ω—Ç –∑–∞–ø–æ–ª–Ω–µ–Ω–∏—è */
        const percent = (props.current / props.target) * 100;
        /** –ü–∞—Ä–∞–º–µ—Ç—Ä—ã –ø–µ—Ä–µ–¥–∞—é—â–∏–µ—Å—è –≤ scoped slot*/
        const params = {
            current: props.current,
            target: props.target,
            percent: percent,
        };
        /** –ü–æ–ª—É—á–∞–µ–º –ø–µ—Ä–µ–¥–∞–≤–∞–µ–º—ã–µ –≤ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç —Å–ª–æ—Ç—ã*/
        const { complete, inprocess } = slots;

        /** –¢–µ–∫—Å—Ç –ø—Ä–∏ –¥–æ—Å—Ç–∏–∂–µ–Ω–∏–∏ –º–∞–∫—Å–∏–º—É–º–∞ –∏–ª–∏ –±–æ–ª—å—à–µ*/
        const completeText = complete
            ? complete({ params })
            : `–°–æ–±—Ä–∞–Ω–æ –±–æ–ª–µ–µ ${params.target} —Ä—É–±.`;

        /** –¢–µ–∫—Å—Ç –ø—Ä–∏ –Ω–µ–¥–æ—Å—Ç–∏–∂–µ–Ω–∏–∏ –º–∞–∫—Å–∏–º—É–º–∞*/
        const inprocessText = inprocess
            ? inprocess({ params })
            : `–°–æ–±—Ä–∞–Ω–æ ${params.current} –∏–∑ ${params.target} —Ä—É–±.`;

        /**–ò—Ç–æ–≥–æ–≤—ã–π —Ç–µ–∫—Å—Ç */
        const text = percent >= 100 ? completeText : inprocessText;

        return (
            <div
                style={`max-width: ${props.maxWidth};`}
                class={[
                    `spui-ProgressBar spui-ProgressBar_${props.size}`,
                    data.class,
                    data.staticClass
                ]}
            >
                <div style={`width: ${percent}%;`} class="spui-ProgressBar__progress"></div>
                <div class="spui-ProgressBar__text">{text}</div>
            </div>
        );
    },
};
</script>

<style lang="scss" scoped>
@import "./ProgressBar.scss";
</style>
