<template>
    <div class="spui-CollapseMultipleSelectList">
        <slot v-if="_isSlotBeforeExist" name="before"></slot>
        <Collapse v-model="_open" :heading="heading" :metainfo="_metainfo">
            <template slot="beforebody">
                <div class="spui-CollapseMultipleSelectList__controls">
                    <span>{{ _selectedPositionsNumber }}</span>
                    <span
                        ><span @click="onSelectAll" class="spui-CollapseMultipleSelectList__all"
                            >–í—ã–±—Ä–∞—Ç—å –≤—Å–µ</span
                        >
                        <span
                            v-show="_displayClearButton"
                            @click="onClear"
                            class="spui-CollapseMultipleSelectList__clear"
                            >–û—á–∏—Å—Ç–∏—Ç—å</span
                        ></span
                    >
                </div>
                <InputSearch
                    placeholder="–ü–æ–∏—Å–∫ –ø–æ –ø–∞—Ä–∞–º–µ—Ç—Ä–∞–º"
                    class="spui-CollapseMultipleSelectList__search"
                    v-model="_inputValue"
                    v-if="displaySearchInput"
                    @onSearch="onSearch"
                ></InputSearch>
            </template>
            <SelectList
                slot="default"
                class="spui-CollapseMultipleSelectList__list"
                v-model="_value"
                :label="label"
                :values="_visibleElements"
                :one="false"
                :fnCompare="fnCompare"
            ></SelectList>
            <template v-show="_displayMoreButton" slot="afterbody">
                <div @click="onChangeExpand" class="spui-CollapseMultipleSelectList__more">
                    {{ listOpen ? "–°–≤–µ—Ä–Ω—É—Ç—å" : "–ü–æ–∫–∞–∑–∞—Ç—å –≤—Å–µ" }}
                </div>
            </template>
        </Collapse>
        <Tooltip
            v-if="_tooltip"
            @click.native="onTooltipClick"
            class="spui-CollapseMultipleSelectList__tooltip"
            type="accent"
            position="right"
            centered
            forced
            >–ü—Ä–∏–º–µ–Ω–∏—Ç—å</Tooltip
        >
        <slot v-if="_isSlotAfterExist" name="after"></slot>
    </div>
</template>

<script>
import { pluralize } from "@/helpers";
import Collapse from "../../common/Collapse/Collapse.vue";
import SelectList from "../../common/SelectList/SelectList.vue";
import Tooltip from "../../common/Tooltip/Tooltip.vue";
import InputSearch from "../../common/InputSearch/InputSearch.vue";

export default {
    name: "spui-CollapseMultipleSelectList",
    components: {
        Collapse,
        SelectList,
        Tooltip,
        InputSearch,
    },
    props: {
        heading: {
            type: String,
            required: true,
            default: "–ò–º—è —Å–µ–∫—Ü–∏–∏ –Ω–µ –ø–µ—Ä–µ–¥–∞–Ω–æ",
        },
        open: {
            type: Boolean,
            default: false,
            required: true,
        },
        listOpen: {
            type: Boolean,
            default: false,
            required: true,
        },
        maxVisibleElements: {
            type: Number,
            default: 8,
        },
        tooltip: {
            type: Boolean,
            default: false,
        },
        values: {
            type: Array,
            required: true,
            default: () => [],
        },
        label: {
            type: Function,
            required: true,
        },
        value: {
            required: true,
        },
        fnCompare: {
            type: Function,
        },
        inputSearchValue: {
            type: String,
            default: "",
        },
        displaySearchInput: {
            type: Boolean,
            default: false,
        },
    },
    computed: {
        _inputValue: {
            get() {
                return this.inputSearchValue;
            },
            set(value) {
                this.$emit("inputSearchValue", value);
            },
        },
        _isSlotBeforeExist() {
            return Boolean(this.$slots["before"]);
        },
        _isSlotAfterExist() {
            return Boolean(this.$slots["after"]);
        },
        /** –ü–æ–∫–∞–∑—ã–≤–∞—Ç—å tooltip –∏–ª–∏ –Ω–µ—Ç */
        _tooltip: {
            get() {
                return this._open && this.tooltip ? true : false;
            },
        },
        /** –ü–æ–∫–∞–∑—ã–≤–∞—Ç—å –∫–Ω–æ–ø–∫—É "–û—á–∏—Å—Ç–∏—Ç—å", –µ—Å–ª–∏ —Ö–æ—Ç—å –æ–¥–∏–Ω –ø–∞—Ä–∞–º–µ—Ç—Ä –≤—ã–±—Ä–∞–Ω */
        _displayClearButton: {
            get() {
                if (!this._value || this._value.length == 0) return false;
                return true;
            },
        },
        /** –ü–æ–∫–∞–∑—ã–≤–∞—Ç—å –∫–Ω–æ–ø–∫—É "–ü–æ–∫–∞–∑–∞—Ç—å –±–æ–ª—å—à–µ/—Å–≤–µ—Ä–Ω—É—Ç—å", –µ—Å–ª–∏ –∑–∞–¥–∞–Ω–æ –æ–≥—Ä–∞–Ω–∏—á–µ–Ω–∏–µ –Ω–∞ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –æ—Ç–æ–±—Ä–∞–∂–∞–µ–º—ã—Ö —ç–ª–µ–º–µ–Ω—Ç–æ–≤ –∏ –æ–Ω–æ –º–µ–Ω—å—à–µ —á–µ–º –ø–æ–ª–Ω—ã–π –ª–∏—Å—Ç */
        _displayMoreButton: {
            get() {
                if (this.maxVisibleElements != 0 && this.maxVisibleElements < this.values.length) {
                    return true;
                }

                return false;
            },
        },
        /** –¢–µ–∫—Å—Ç - –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –≤—ã–±—Ä–∞–Ω–Ω—ã—Ö –ø–æ–∑–∏—Ü–∏–π */
        _selectedPositionsNumber: {
            get() {
                if (!this._value) return;
                const selected = this._value.length;
                const all = this.values.length;

                switch (selected) {
                    case 0:
                        return "–í—ã–±–µ—Ä–∏—Ç–µ –ø–∞—Ä–∞–º–µ—Ç—Ä";
                        break;

                    case all:
                        return "–í—ã–±—Ä–∞–Ω–æ –≤—Å–µ";
                        break;

                    default:
                        return `${selected} ${pluralize(selected, [
                            "–ø–æ–∑–∏—Ü–∏—è",
                            "–ø–æ–∑–∏—Ü–∏–∏",
                            "–ø–æ–∑–∏—Ü–∏–π",
                        ])}`;
                        break;
                }
            },
        },
        /** –†–∞—Å–∫—Ä—ã—Ç–∞ —Å–µ–∫—Ü–∏—è –∏–ª–∏ –Ω–µ—Ç */
        _open: {
            get() {
                return this.open;
            },
            set(value) {
                this.$emit("collapse", value);
            },
        },
        /** –í—ã–±—Ä–∞–Ω–Ω–æ–µ –∑–Ω–∞—á–µ–Ω–∏–µ –ø–∞—Ä–∞–º–µ—Ç—Ä–æ–≤ - –º–∞—Å—Å–∏–≤ –≤—ã–±—Ä–∞–Ω–Ω—ã—Ö —Ö–∞—Ä–∞–∫—Ç–µ—Ä–∏—Å—Ç–∏–∫ */
        _value: {
            get() {
                return this.value;
            },
            set(value) {
                this.$emit("input", value);
                this.$emit("tooltipStateChange", true);
            },
        },
        /** –ú–µ—Ç–∞ –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è –æ –≤—ã–±—Ä–∞–Ω–Ω—ã—Ö –ø–∞—Ä–∞–º–µ—Ç—Ä–∞—Ö –ø—Ä–∏ —Å–æ–∫—Ä—ã—Ç–∏–∏ —Å–µ–∫—Ü–∏–∏ */
        _metainfo: {
            get() {
                if (this._value && this._value.length) {
                    const str = this._value.reduce((acc, el) => {
                        return acc + this.label(el) + ", ";
                    }, "");
                    const sliced = str.slice(0, str.length - 2);
                    return sliced;
                }
            },
        },
        /** –ö–æ–ª–∏—á–µ—Å—Ç–≤–æ –≤–∏–¥–∏–º—ã—Ö —ç–ª–µ–º–µ–Ω—Ç–æ–≤ –≤ —Å–µ–∫—Ü–∏–∏ */
        _visibleElements: {
            get() {
                if (this.maxVisibleElements > 0 && !this.listOpen) {
                    return this.values.slice(0, this.maxVisibleElements);
                }

                if (this.maxVisibleElements > 0 && this.listOpen) {
                    return this.values;
                }

                return this.values;
            },
        },
    },
    methods: {
        onClear() {
            this.$emit("input", []);
            this.$emit("tooltipStateChange", true);
        },
        onSelectAll() {
            this.$emit("input", this.values);
            this.$emit("changeListOpen", true);
            this.$emit("tooltipStateChange", true);
        },
        onChangeExpand() {
            this.$emit("changeListOpen", !this.listOpen);
        },
        onTooltipClick() {
            this.$emit("tooltipClick");
        },
        onSearch(value) {
            this.$emit("onSearch", value);
        },
    },
};
</script>

<style lang="scss" scoped>
@import "./CollapseMultipleSelectList.scss";
</style>
